import React from "react";
import Carouselavis from "../components/carousel/CarouselAvis";
import Formcontact from "../components/Form/FormContact";
import "./Home.css";

function Home() {
  return (
    <div className="public-home">
      <div className="welcome-banner"></div>
      <div className="section-row" id="presentation">
        <div className="content-image1"></div>
        <div className="content-text1">
          <p>
            <p>Qu’est-ce qu’Alt’izY ?</p> <p>Alt’izY est une plateforme qui met en
            relation les étudiants à la recherche d’une alternance ou d’un stage
            avec des entreprises qui recrutent. Notre mission aujourd’hui, est
            d’aider les étudiants à trouver l’alternance ou le stage qui leur
            correspond. Vous êtes une entreprise ? Nous sommes la solution pour
            vos recrutements. </p>
          </p>
        </div>
      </div>

      <div className="section-column" id="equipe">
        <div className="title-image">
          <div className="title">
            <h1>
              Notre <br></br>équipe
            </h1>
          </div>
          <div className="content-image2"></div>
        </div>
        <div className="content-text2">
          <p>
            Nous sommes une équipe de 5 étudiants, nous sommes tous passés par
            l’alternance, nous savons que ce n’est pas toujours facile. C’est
            pour cette raison que nous avons décidé de créer Alt’izY. Alors
            aujourd’hui, retrouvez, Manon, Chloé, Johanna, Agénor et Alexandre
            pour trouver l’alternance qui vous convient!
          </p>
        </div>
      </div>

      <Carouselavis />
      <Formcontact />
    </div>
  );
}

export default Home;
